	module.exports = {"data":{"contentfulBlog":{"title":"Is it possible to be tired and happy? 10 things to consider when evaluating energy and tension.","content":{"childMarkdownRemark":{"html":"<blockquote>\n<p><em><a href=\"http://eepurl.com/c5qLKv\">Sign up to get emailed updates</a></em></p>\n</blockquote>\n<p>This past weekend I completed the Bourbon Chase with a team of eleven other runners, two drivers and two volunteers. It is one of the more enjoyable running events that I get to do. This year I participated with a new team. It is always fun to meet new people when it is centered around a memorable event. While it was fun, I did not really plan my participation incredibly well. In fact, until Craig (our team captain) started to send out reminders of the event, I forgot that I had committed to be on the team.</p>\n<p>The main reason that I was nervous about doing the event, is that I had just finished the Crazy Horse Marathon five days before the start of the Bourbon Chase. And two weeks prior to that, I completed the Sundance to Spearfish Marathon. I had no expectation of what might be possible or how my body would respond. As I started my first run on Friday afternoon, I felt pretty good. I had energy. My legs were a bit weak which made the downhills feel out of control, but my performance was as good as I could have wished. I also experienced the desire to perform for my team.</p>\n<p>I ended the Bourbon Chase feeling tired, overly fatigued and a little absent minded. I had not done a good job with nutrition throughout the event, therefore I was also dehydrated and experiencing gastro-intestinal issues.</p>\n<p>Did I go home and sleep for twenty-four hours? I did not. After being gone for six weeks, I was anxious to see friends. I went to dinner with some friends for a birthday celebration. I hung out with a friend until a little later in the night. Then after some restless sleep got up and spent my Sunday meeting old friends and spending time with someone I had been waiting 6 weeks to see again.</p>\n<p>Sunday night, I finally sat down, put up my feet and started to watch a show on Netflix. It was at that moment that I realized how incredibly tired I truly was. It is now Tuesday morning and I am still a little fatigued, something I’m trying to rectify with coffee.</p>\n<p>I share this entire experience with you, because it had me asking the question:</p>\n<blockquote>\n<p>Is it possible to be happy when you are fatigued?</p>\n</blockquote>\n<p>To answer this question, I reflect back on the research and work by Robert Thayer, PhD. I initially came across his research when I was in graduate school and researching the connections between mood states and exercise. In his research, he was testing to see if it was possible to substitute a positive behavior, such as a five minute walking break for a negative behavior, such as smoking or eating high sugar candy.</p>\n<p>The model that he developed involves two spectrums. A spectrum of calm to tense and another of tired to energetic. The theory is that individuals adopt behaviors or habits that help manage their position on the spectrums. For example, if you have low energy you may try and improve it by eating a sugary snack. If you are tense, you may try and decrease that tension by smoking a cigarette.</p>\n<p>The ultimate state of being, as viewed through this model, would be possessing calm energy. Calm Energy happens to be the name of one of the books by Robert Thayer that distills his research in an easy to consume manner.</p>\n<p>When I think about the concept of having calm energy, it is feels pretty intuitive that when people are tired, they have less margin for error and experiencing happiness. The energy needed to manage stressors, daily life and challenges is simply not large enough to be effective. I am sure that we’ve all witnessed a tired tyrant.</p>\n<p>The application of the Calm Energy theory has many different avenues that we’ll explore over time, as we continue to think about behavior change and happiness. Today my goal was simply to pose the question for you to reflect upon, can we be tired and happy?</p>\n<p>I think it is possible to be tired and happy. I was really tired on Saturday night, but happy as I meet with friends. On Sunday, I was exhausted but happy. In both instances I didn’t have much vigor, so it’s possible I wasn’t the best company, but I also did not have much negative stress or tension on either day. What if I had been faced with a major stressful event? I might have become an angry or sad person quite easily.</p>\n<p>When fatigue and stress become chronic situations, I believe it can lead to longer lasting issues, such as depression and anxiety.</p>\n<blockquote>\n<p>Can we be tired and happy? Yes, but it’s a lot like walking a tight rope with nothing to aid your balance.</p>\n</blockquote>\n<p><strong>Here are 10 things to consider as you evaluate your state of calmness, fatigue and happiness.</strong></p>\n<ol>\n<li>Are you getting to bed at an appropriate time of night?</li>\n<li>Have you created an optimal environment for a good night of sleep?</li>\n<li>Do you find yourself reaching for supplemental sources of energy often?</li>\n<li>Are you getting regular exercise?</li>\n<li>Are you getting too much exercise, with signs of overtraining?</li>\n<li>Do you take recovery from exercise as serious as you take your training routines?</li>\n<li>Are your eating habits consistent, or varied day-to-day?</li>\n<li>How many times in a day do you sit for longer than one hour?</li>\n<li>Have you established healthy ways to manage your stress?</li>\n<li>Do you have a way to regularly monitor your level of fatigue?</li>\n</ol>\n<p><strong>Resources</strong>, two books by Robert Thayer</p>\n<ul>\n<li><a href=\"https://www.amazon.com/dp/B000VIJ0VQ/ref=dp-kindle-redirect?_encoding=UTF8&#x26;btkr=1\">Calm Energy: How people regulate mood with food and energy</a></li>\n<li><a href=\"https://www.amazon.com/dp/B000SBEBLA/ref=dp-kindle-redirect?_encoding=UTF8&#x26;btkr=1\">The Origins of Everyday Moods: Managing energy, tension and stress</a></li>\n</ul>"}}},"allContentfulLesson":{"edges":[{"node":{"title":"Week One","week":1,"slug":"week-one","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184/sets/happier-and-healthier-you"}},{"node":{"title":"Week Two","week":2,"slug":"week-two","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Three","week":3,"slug":"week-three","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Four","week":4,"slug":"week-four","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184/sets/happier-and-healthier-you"}},{"node":{"title":"Week Five","week":5,"slug":"week-five","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Six","week":6,"slug":"week-six","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Seven","week":7,"slug":"week-seven","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Eight","week":8,"slug":"week-eight","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Nine","week":9,"slug":"week-nine","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Ten","week":10,"slug":"week-ten","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Eleven","week":11,"slug":"week-eleven","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Twelve","week":12,"slug":"week-twelve","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Thirteen","week":13,"slug":"week-thirteen","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}}]}},"pathContext":{"slug":"is-it-possible-to-be-tired-and-happy"}}

